.backdrop {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  visibility: visible;
  z-index: 10;
  background-color: #fffc;
  transition-property: scale, opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: ease;
  position: fixed;
  top: 0;
  left: 0;
  scale: 1;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  scale: 0;
}

